import { Component } from '@angular/core';
import { NavController, PopoverController, ToastController } from 'ionic-angular';
import { SaccoAuthPopOverPage } from "../sacco-auth-pop-over/sacco-auth-pop-over";
import { SaccoownerPage } from "../saccoowner/saccoowner";
import { DriverpopPage } from "../driverpop/driverpop";
import { LocalData } from "../../services/localdata";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(
    public navCtrl: NavController, 
    public popoverCtrl: PopoverController,
    public local: LocalData,
    public toast: ToastController
  ) {

    if(localStorage.getItem("email") == null || localStorage.getItem("email") == undefined) {
      //do nothing to allow login
    } else {
      this.local.saccologin(localStorage.getItem("email"), localStorage.getItem("password")).subscribe(resp => {
        if(resp.ok == true) {
          localStorage.setItem("email", resp.data.email);
          localStorage.setItem("password", resp.data.password);
          localStorage.setItem("username", resp.data.username);

          this.navCtrl.push(SaccoownerPage, {
            email: resp.data.email,
            username: resp.data.username
          });
        } else {
          //do nothing to allow login.
        }
      })
    }
  }

  presentSaccoOwnerAuth(event) {
    let popover = this.popoverCtrl.create(SaccoAuthPopOverPage, {}, {cssClass: 'custom-popover'});
    popover.present({
      ev: event
    });
  }

  presentDriverAuth() {
    let popover = this.popoverCtrl.create(DriverpopPage, {}, {cssClass: 'custom-popover'});
    popover.present();
  }

  gotoHome() {
    this.navCtrl.push(SaccoownerPage);
  }

}



