import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
import { LocalData } from "../../services/localdata";

@IonicPage()
@Component({
  selector: 'page-addbus',
  templateUrl: 'addbus.html',
})
export class AddbusPage {

  username: String;
  email: String;
  numberplate: String;
  busmodel: String;
  route: String;
  seats: Number;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public view: ViewController,
    public toast: ToastController,
    public local: LocalData
  ) 
  {
    this.username = this.navParams.get("username");
    this.email = this.navParams.get("email");
  }

  ionViewDidLoad() {
    
  }

  addNewBus(e) {
    if(this.numberplate == undefined) {
      let toast = this.toast.create({
        message: "Fill in the numberplate field",
        cssClass: "danger",
        duration: 2000,
        position: "top"
      });
      toast.present();
    } else if(this.route == undefined) {
      let toast = this.toast.create({
        message: "Fill in the route field",
        cssClass: "danger",
        duration: 2000,
        position: "top"
      });
      toast.present();
    } else if(this.seats == undefined) {
      let toast = this.toast.create({
        message: "Fill in the bus seats field",
        cssClass: "danger",
        duration: 2000,
        position: "top"
      });
      toast.present();
    } else {
      this.local.addBus(this.numberplate, this.username, this.email, this.route, this.seats, this.busmodel)
      .subscribe(response => {
        this.view.dismiss({buses: response.data}, "popover");
      })
    }
  }

}



