import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { LocalData } from "../../services/localdata";

@IonicPage()
@Component({
  selector: 'page-driverhome',
  templateUrl: 'driverhome.html',
})
export class DriverhomePage {

  driver: Driver;
  username: String;
  email: String;
  available: Boolean;

  //td Trip Destination
  //at Arrival Time
  //ss Starting Stage
  //dt Departure Time
  td: String;
  at: Date;
  ss: String;
  dt: Date;

  constructor(public navCtrl: NavController, public navParams: NavParams, private local: LocalData, private toast: ToastController) {
    this.driver = {
      _id: this.navParams.get("id"),
      numberplate: this.navParams.get("numberplate"),
      username: this.navParams.get("username"),
      email: this.navParams.get("email"),
      phonenumber: this.navParams.get("phonenumber"),
      currentRoute: this.navParams.get("currentRoute"),
      available: this.navParams.get("available"),
      currently_from: this.navParams.get("currently_from"),
      currently_to: this.navParams.get("currently_to"),
      employed: this.navParams.get("employed"),
      on_route: this.navParams.get("on_route"),
      reviews: this.navParams.get("reviews"),
      employer: this.navParams.get("employer")
    };

  }

  ionViewDidLoad() {
    
  }

  setTrip() {
    if(this.driver.numberplate == null || this.driver.numberplate == undefined || this.driver.numberplate == "") {
      let warning = this.toast.create({
        message: "You do not have a bus to drive",
        duration: 3000
      });
      warning.present();
    } else {
      this.local.setTrip(this.driver.numberplate, this.at, this.dt, this.td, this.ss).subscribe(response => {
        console.log(response);
        if(response.data.ok == true) {
          alert("Notify driver trip has begun");
        } else {
          alert(response.data.message);
        }
      })
    }
  }

}

interface Driver {
  _id: String,
  numberplate: String,
  username: String,
  email: String,
  phonenumber: Number,
  currentRoute: String,
  available: Boolean,
  employed: Boolean,
  on_route: Boolean,
  currently_to: String,
  currently_from: String,
  reviews: [{
      username: String,
      star: Number,
      comment: String
  }],
  employer: {
      employername: String,
      employermail: String
  }
}



