import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController, ToastController, AlertController } from 'ionic-angular';
import { AddbusPage } from "../addbus/addbus";
//import { BusdetailPage } from "../busdetail/busdetail";
import { HomePage } from "../home/home";

import { LocalData } from "../../services/localdata";

@IonicPage()
@Component({
  selector: 'page-saccoowner',
  templateUrl: 'saccoowner.html',
})
export class SaccoownerPage {


  drivers: Driver[];
  buses: Bus[];
  username: String;
  email: String;
  activebus: Bus;

  constructor(
    public navCtrl: NavController, private toast: ToastController, private alt: AlertController,
    public navParams: NavParams, 
    public lD: LocalData, 
    public popover: PopoverController,
    public modal: ModalController
  ) 
  {
    this.username = this.navParams.get("username");
    this.email = this.navParams.get("email");
    //should save to localStorage
    if(this.navParams.get("buses") == undefined) {
      //undefined so get from web.
      this.lD.getBuses(this.username).subscribe(buses => {
        this.buses = buses.data;
      })
    } else {
      //use the the ones
      this.buses = this.navParams.get("buses");
    }
  }

  ionViewDidLoad() {}

  addbus() {
    let popover = this.popover.create(AddbusPage, {
      username: this.username,
      email: this.email
    }, {cssClass: 'custom-popover'});
    popover.present();
    popover.onDidDismiss(data => {
      if(data == null) {

      } else {
        this.buses = data.buses;
      }
      console.log(data);
    })
  };

  busDetail(numberplate) {
    //get bus details and update activebus object with details
    this.lD.busDetail(numberplate).subscribe(details => {
      console.log(details.data);
      this.activebus = details.data;
    })
  }

  editRoute(id, oldroute) {
    var route = prompt("Please assign your bus a new route name", oldroute);
    this.lD.updateRoute(id, route).subscribe(response => {
      this.activebus = response.data;
    })
  }

  //add stop
  addStop(id, route) {
    var stop = prompt("Enter a new Bus Stop name for this Route", "");
    this.lD.addStop(id, stop, route).subscribe(response => {
      this.activebus = response.data;
    })

  }

  showAvailableDrivers() {
    this.lD.availabledrivers().subscribe(data => {
      console.log(data);
      if(data.ok == false) {
        let mytoast = this.toast.create({
          message: data.message
        });
        mytoast.present();
      } else {
        var radioAlt = this.alt.create();
        radioAlt.setTitle("Pick a driver");

        data.data.forEach(function(driver) {
          radioAlt.addInput({
            type: "radio",
            label: driver.username,
            value: driver.email
          });
        });

        radioAlt.addButton("Cancel");
        radioAlt.addButton({
          text: "Hire",
          handler: data => {
            alert(this.activebus.numberplate);
            alert("I have picked Driver " + data);
            this.lD.hiredriver(this.activebus.numberplate, data).subscribe(myresp => {
              console.log(myresp);
            })
          }
        });
        radioAlt.present();
      }
    })
  }

  logout() {
    localStorage.removeItem("username");
    localStorage.removeItem("email");
    localStorage.removeItem("password");

    this.navCtrl.push(HomePage);
  }

}

interface Driver {
  numberplate: string;
  drivername: string;
  phonenumber: number;
  router: string;
}

interface Bus {
  numberplate: String,
  ownername: String,
  route: String,
  to: String,
  from: String,
  passengers: Number,
  seats: Number,
  currentStop: String,
  arrival: Date,
  departure: Date,
  model: String,
  ownermail: String,
  reviews: any[],
  stops: any[],
  _id: String
}



