import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
import { SaccoownerPage } from "../saccoowner/saccoowner";
import { LocalData } from "../../services/localdata";

@IonicPage()

@Component({
  selector: 'page-sacco-auth-pop-over',
  templateUrl: 'sacco-auth-pop-over.html',
})
export class SaccoAuthPopOverPage {

  auth: String;
  susername: String;
  spassword: String;
  semail: String;
  lpassword: String;
  lemail: String;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public vc: ViewController, 
    public local: LocalData,
    public toast: ToastController) 
  {
    this.auth = "login";
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SaccoAuthPopOverPage');
  }

  closePopup() {
    this.vc.dismiss();
  }

  login() {
    if(this.lemail == undefined || this.lpassword == undefined) {
      alert("Fill in all values Bro");
    } else {
      this.local.saccologin(this.lemail, this.lpassword).subscribe(response => {
        if(response.ok == true) {
          this.vc.dismiss();
          localStorage.setItem("email", response.data.email);
          localStorage.setItem("password", response.data.password);
          localStorage.setItem("username", response.data.username);
          this.navCtrl.push(SaccoownerPage, {
            email: this.lemail,
            username: response.data.username
          });
        } else {
          let toast = this.toast.create({
            message: "Wrong email and password combition",
            duration: 3000,
            position: "bottom"
          });
          toast.present();
        }
      });
    }
  }

  signup() {
    if(this.susername == undefined || this.semail == undefined || this.spassword == undefined) {
      alert("Fill in all values Bro");
    } else {
      this.local.saccosignup(this.susername, this.semail, this.spassword).subscribe(response => {
        if(response.ok == true) {
          this.vc.dismiss();
          localStorage.setItem("email", response.data.email);
          localStorage.setItem("password", response.data.password);
          localStorage.setItem("username", response.data.username);
          this.navCtrl.push(SaccoownerPage, {
            username: response.data.username,
            email: response.data.email
          });
        } else {
          let toast = this.toast.create({
            message: "User already exists, would you like to login?",
            duration: 7000,
            position: "bottom",
            showCloseButton: true,
            closeButtonText: "Login",
            dismissOnPageChange: true
          });
          toast.onDidDismiss(() => {
            this.auth = "login";
          })
          toast.present();
        }
      });
    }
  }

}



